import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import "../../styles/main/style.css";

import Rules from "./Rules";
import Gameplay from "./Gameplay";
import Creators from "./Creators";

function GamePage({ questions, adminMode }) {
  return (
    <>
      <Router>
        <Switch>
          <div className="game-page">
            <Route exact path={process.env.PUBLIC_URL + "/"}>
              <Rules adminMode={adminMode} />
            </Route>
            <Route path={process.env.PUBLIC_URL + "/game"}>
              <Gameplay questions={questions} adminMode={adminMode} />
            </Route>
            <Route path={process.env.PUBLIC_URL + "/creators"}>
              <Creators adminMode={adminMode} />
            </Route>
          </div>
        </Switch>
      </Router>
    </>
  );
}

export default GamePage;
